.form {
  @include flex-v-center;

  width: 293px;
  height: 33px;

  &__input {
    flex-grow: 1;
    border: none;
    border-radius: 6px 0 0 6px;
    padding: 11px;
    height: 100%;
    font-size: 12px;
    color: var(--gray-color);
  }

  &__btn {
    border-radius: 0 6px 6px 0;
    padding: 0 15px;
    height: 100%;
    font-size: 12px;
    text-transform: uppercase;
    color: var(--light-color);
    background-color: var(--main-color);
  }
}
